$col_off : #8B635C;
$col_dark:#210124;
$col_main:#5B7553;
$col_light:#D5FFD9;
$col_in : #547AA5;
$col_lighter:#ffffffaa;

$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";




@font-face{
    font-family:Pinyon;
    src:url($baseurl +"/assets/font/PinyonScript-Regular.ttf");
}


body {
    background:#38632e;
    background:linear-gradient(to bottom,#38632e,#cfd0b1);
    background:-webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, #38632e), color-stop(100%, #cfd0b1));
    background: $col_main;
    color: black;
    font-family: $bodyFont;
    background-repeat:no-repeat;
    background-size:cover;
    background-position:center center;
    background-attachment:fixed;
    font-family:verdana, arial;
}

.sidebar {
  position:fixed;
  background-color:$col_in;
  top:0px;
  bottom:0px;
  left:0px;
  width:20vw;
  min-width:300px;
  @include media-query($on-palm) {
    position:static;
    top:auto;
    bottom:auto;
    left:auto;
    width:100%;
    min-height:120px;
  }
}

.sidebar img{
  margin-top:30px;
  width:20vw;
  min-width:300px;
  @include media-query($on-palm){
    margin-top:0px;
    position:static;
    float:left;
    width:120px;
    min-width:120px;
  }

}

.contents{
  margin-left:calc(max(20vw, 300px));
  background-color:$col_light;
  padding:15px;
  min-height:50vw;
  @include media-query($on-palm){
    margin-left:10px;
    margin:10px;
  }
}



.ancient {
    max-width:220px;
    margin-left:10px;
    margin-bottom:10px;
    font-size:25px;
    font-weight:bold;
    float:right;
    //background:rgba(0,0,0,0.5);
    //text-align:right;
}

.ancient span{
    font-family:Pinyon;

}

.notice {
  background-color:$col_lighter;
  border:1px solid black;
  padding:4px;
  margin:10px;
  width:fit-content;
}

.member img {
  width:120px;
  float:left;
  margin:10px;
}

